const EmbedVideo = ({url}) =>
  <iframe
    title="Bohemian Rhapsody"
    width="100%"
    height="100%"
    src={url}
    frameBorder="0"
    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
    allowFullScreen
  />;

const arrPages = [
  <iframe width="300" height="300" title="SpacetimeQ" src="https://www.spacetimeq.com"></iframe>,
  <EmbedVideo url="https://www.youtube.com/embed/fJ9rUzIMcZQ?controls=0" />,
  <img src="images/shibaHelp.gif" width="100%" alt="Shiba Help" />,
  <iframe width="300" height="300" title="Cal" src="https://spacetimeq.github.io"></iframe>,
  <EmbedVideo url="https://www.youtube.com/embed/T73WhWTawCE" />,
  <EmbedVideo url="https://www.youtube.com/embed/04854XqcfCY?controls=0" />,
];

const Sym3D = () => {
  const MODES = 4;
  const [mode, setMode] = useState(0);

  const cell = { w: 300, h: 300 };
  const m = 10;

  const s3Cell = (page, i) => {
    const r = cell.w / 2;
    const trz = [  // translate
      {x: -cell.w-m,   y: -cell.h-m},
      {x: 0,           y: -cell.h-m},
      {x: cell.w+m,    y: -cell.h-m},
      {x: -cell.w-m,   y: 0},
      {x: 0,           y: 0},
      {x: cell.w+m,    y: 0},
    ];
    const rot = [  // rotation degrees
      {x: 0,   y: 0},
      {x: 0,   y: 90},
      {x: 0,   y: 180},
      {x: 0,   y: 270},
      {x: 90,  y: 0},
      {x: -90, y: 0}
    ];
    const crs_r = Math.round( cell.w / ( 2 * Math.tan(Math.PI / 6) ) );   // carousel radius
    const rotC = 360/6;
    const crs = [  // carousel
      {x: 0, y: 0},
      {x: 0, y: rotC},
      {x: 0, y: rotC*2},
      {x: 0, y: rotC*3},
      {x: 0, y: rotC*4},
      {x: 0, y: rotC*5}
    ];
    const transformCell = () => {
      switch (mode) {
        case 0: // Flat
          return `translateX(${trz[i].x}px) translateY(${trz[i].y}px)`;
        case 1: // cube
          return (
            ( rot[i].x ? `rotateX(${rot[i].x}deg) ` : '') +
            ( rot[i].y ? `rotateY(${rot[i].y}deg) ` : '') +
            `translateZ(${r}px)`
          );
        case 2: // Stack
          return `translateX(${-r + i*50}px) translateY(${-r + i*50}px) translateZ(${r/2-i*50}px)`;
        case 3: // carousel
          return (
            ( crs[i].x ? `rotateX(${crs[i].x}deg) ` : '') +
            ( crs[i].y ? `rotateY(${crs[i].y}deg) ` : '') +
            `translateZ(${crs_r}px)`
          );
        default:
          return;
      }
    };

    return (
      <div key={i} className="s3cell"
        style={{
          transform: transformCell(i),
        }}
      >
        {page}
      </div>
    );
  }

/*
    const lns = [];
    arrPages.forEach( (pg, i) => lns.push(<Cell key={i} i={i}>{pg}</Cell>) )
        {arrPages.map( (pg, i) => <Cell key={i} i={i}>{pg}</Cell> )}
*/
  return (
    <>
      <div className="s3scene" >
        <div className="s3body">
          {arrPages.map( (pg, i) => s3Cell(pg, i) )}
        </div>
      </div>
      <div className="btn-container">
        <TBtn onClick={()=>setMode((mode + 1) % MODES)}>Transform</TBtn>
      </div>
    </>
  );
}

const TBtn = props => <button type="button" className="test-btn" {...props}>{props.children}</button>;
        /*
          style={{
            transform: "rotateX(-30deg)"
          }}
        */

function App() {
  const ws = useWindowSize();

  return (
    <div className="App" >
      <StarryNight width={ws.w} height={ws.h} />
      <Sym3D />
    </div>
  );
}

export default App;
